"use client"
import React, { useState } from 'react';
import { ChevronRight, Download, FileText, FolderOpen, Map, Clock, Target, CheckCircle, Circle } from 'lucide-react';

const ProductDesignerRoadmap = () => {
  const [activeTab, setActiveTab] = useState('roadmap');
  const [completedItems, setCompletedItems] = useState(new Set());
  const [expandedPhases, setExpandedPhases] = useState(new Set([1])); // First phase expanded by default

  const phases = [
    {
      id: 1,
      title: "Design Foundations",
      level: "Beginner",
      duration: "2-3 months",
      description: "Master visual design principles and basic design thinking",
      priority: "Essential",
      sections: [
        {
          title: "Visual Design Principles",
          items: [
            "Visual hierarchy and contrast principles",
            "Typography fundamentals and pairing",
            "Color theory and accessibility standards",
            "Layout composition and grid systems"
          ]
        },
        {
          title: "Design Process",
          items: [
            "Double Diamond methodology",
            "Human-centered design approach",
            "Problem definition and solution framing"
          ]
        },
        {
          title: "Core Tools",
          items: [
            "Figma mastery (components, auto-layout)",
            "Collaborative whiteboarding (FigJam, Miro)",
            "Icon libraries and asset management"
          ]
        },
        {
          title: "Practice Projects",
          items: [
            "App redesign case study",
            "Landing page design system",
            "Component library creation"
          ]
        }
      ]
    },
    {
      id: 2,
      title: "UX Research & Strategy",
      level: "Intermediate",
      duration: "3-4 months",
      description: "Develop user research skills and strategic thinking",
      priority: "Critical",
      sections: [
        {
          title: "User Research Methods",
          items: [
            "User interviews and survey design",
            "Persona development and empathy mapping",
            "Competitive analysis frameworks",
            "User journey and experience mapping"
          ]
        },
        {
          title: "Information Architecture",
          items: [
            "Card sorting and taxonomy creation",
            "Navigation design and hierarchy",
            "Content organization strategies"
          ]
        },
        {
          title: "Wireframing & Prototyping",
          items: [
            "Low to high-fidelity wireframe progression",
            "Interactive prototype creation",
            "Micro-interaction design principles"
          ]
        },
        {
          title: "Accessibility & Inclusion",
          items: [
            "WCAG 2.2 compliance standards",
            "Inclusive design methodologies",
            "Cross-platform accessibility testing"
          ]
        }
      ]
    },
    {
      id: 3,
      title: "Product Design Mastery",
      level: "Intermediate",
      duration: "4-5 months",
      description: "Become a strategic product designer with business acumen",
      priority: "Strategic",
      sections: [
        {
          title: "Advanced UI Systems",
          items: [
            "Scalable design system architecture",
            "Component-driven design methodology",
            "Design token implementation",
            "Multi-brand and dark mode systems"
          ]
        },
        {
          title: "Product Strategy",
          items: [
            "Business goal alignment and KPI definition",
            "Growth design and retention strategies",
            "A/B testing design and hypothesis formation",
            "Feature prioritization frameworks"
          ]
        },
        {
          title: "Cross-functional Collaboration",
          items: [
            "Developer handoff and design specs",
            "Product manager partnership strategies",
            "Agile/Scrum integration for designers",
            "Stakeholder communication and buy-in"
          ]
        }
      ]
    },
    {
      id: 4,
      title: "Advanced Practice",
      level: "Advanced",
      duration: "3-4 months",
      description: "Master advanced techniques and industry best practices",
      priority: "Professional",
      sections: [
        {
          title: "Data-Driven Design",
          items: [
            "Analytics integration and interpretation",
            "Conversion rate optimization techniques",
            "User testing and validation methods",
            "Performance metrics and design impact"
          ]
        },
        {
          title: "Design Leadership",
          items: [
            "Design critique facilitation",
            "Storytelling and presentation skills",
            "Team mentorship and knowledge sharing",
            "Design culture development"
          ]
        },
        {
          title: "Specialization Areas",
          items: [
            "Interaction design and motion principles",
            "Content strategy and UX writing",
            "Service design and ecosystem thinking",
            "AI-enhanced design workflows"
          ]
        },
        {
          title: "Professional Tools",
          items: [
            "Advanced prototyping (Principle, Framer)",
            "Analytics platforms (Mixpanel, Amplitude)",
            "Collaboration tools (Notion, Linear, GitHub)",
            "Design system management tools"
          ]
        }
      ]
    },
    {
      id: 5,
      title: "Career Development",
      level: "Professional",
      duration: "Ongoing",
      description: "Build your career and establish professional presence",
      priority: "Career",
      sections: [
        {
          title: "Portfolio Excellence",
          items: [
            "Case study storytelling and documentation",
            "Process visualization and impact metrics",
            "Problem-solving narrative development",
            "Visual presentation and layout mastery"
          ]
        },
        {
          title: "Professional Branding",
          items: [
            "LinkedIn optimization and networking",
            "Personal website and blog development",
            "Design community engagement",
            "Thought leadership content creation"
          ]
        },
        {
          title: "Interview Preparation",
          items: [
            "Portfolio presentation skills",
            "Design challenge problem-solving",
            "Product thinking and strategy questions",
            "Cultural fit and collaboration scenarios"
          ]
        },
        {
          title: "Continuous Growth",
          items: [
            "Industry trend monitoring and adaptation",
            "Continuous learning and skill development",
            "Professional network expansion",
            "Leadership and mentorship opportunities"
          ]
        }
      ]
    }
  ];

  const togglePhase = (phaseId) => {
    const newExpanded = new Set(expandedPhases);
    if (newExpanded.has(phaseId)) {
      newExpanded.delete(phaseId);
    } else {
      newExpanded.add(phaseId);
    }
    setExpandedPhases(newExpanded);
  };

  const toggleItem = (phaseId, sectionIndex, itemIndex) => {
    const itemId = `${phaseId}-${sectionIndex}-${itemIndex}`;
    const newCompleted = new Set(completedItems);
    if (newCompleted.has(itemId)) {
      newCompleted.delete(itemId);
    } else {
      newCompleted.add(itemId);
    }
    setCompletedItems(newCompleted);
  };

  const getPhaseProgress = (phase) => {
    const totalItems = phase.sections.reduce((sum, section) => sum + section.items.length, 0);
    const completedPhaseItems = Array.from(completedItems).filter(item => 
      item.startsWith(`${phase.id}-`)
    ).length;
    return totalItems > 0 ? Math.round((completedPhaseItems / totalItems) * 100) : 0;
  };

  const getPriorityColor = (priority) => {
    const colors = {
      'Essential': 'bg-red-100 text-red-800 border-red-200',
      'Critical': 'bg-orange-100 text-orange-800 border-orange-200',
      'Strategic': 'bg-blue-100 text-blue-800 border-blue-200',
      'Professional': 'bg-purple-100 text-purple-800 border-purple-200',
      'Career': 'bg-green-100 text-green-800 border-green-200'
    };
    return colors[priority] || 'bg-gray-100 text-gray-800 border-gray-200';
  };

  return (
    <div className="min-h-screen bg-white">
      {/* Fixed Header */}
      <div className="sticky top-0 z-50 bg-white border-b border-gray-200">
        <div className="max-w-6xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">
                Product Designer Roadmap
              </h1>
              <p className="text-sm text-gray-600 mt-1">
                Complete learning path from beginner to professional
              </p>
            </div>
            <button className="bg-gray-900 hover:bg-gray-800 text-white px-4 py-2 rounded-lg flex items-center space-x-2 transition-colors">
              <Download size={18} />
              <span>Export PDF</span>
            </button>
          </div>

          {/* Navigation */}
          <div className="flex space-x-1 mt-4 bg-gray-100 p-1 rounded-lg w-fit">
            {[
              { key: 'roadmap', label: 'Roadmap', icon: Map },
              { key: 'documentation', label: 'Resources', icon: FileText },
              { key: 'projects', label: 'Projects', icon: FolderOpen }
            ].map(({ key, label, icon: Icon }) => (
              <button
                key={key}
                onClick={() => setActiveTab(key)}
                className={`px-4 py-2 rounded-md font-medium text-sm transition-all flex items-center space-x-2 ${
                  activeTab === key
                    ? 'bg-white text-gray-900 shadow-sm'
                    : 'text-gray-600 hover:text-gray-900'
                }`}
              >
                <Icon size={16} />
                <span>{label}</span>
              </button>
            ))}
          </div>
        </div>
      </div>

      <div className="max-w-6xl mx-auto px-6 py-8">
        {activeTab === 'roadmap' && (
          <div className="space-y-8">
            {/* Progress Overview */}
            <div className="bg-gray-50 rounded-xl p-6 border border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Learning Progress</h2>
              <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
                {phases.map((phase) => {
                  const progress = getPhaseProgress(phase);
                  return (
                    <div key={phase.id} className="text-center">
                      <div className="relative w-16 h-16 mx-auto mb-2">
                        <div className="w-16 h-16 bg-gray-200 rounded-full"></div>
                        <div 
                          className="absolute top-0 left-0 w-16 h-16 bg-blue-600 rounded-full transition-all duration-500"
                          style={{
                            clipPath: `polygon(50% 50%, 50% 0%, ${
                              progress >= 50 ? '100%' : `${50 + progress}%`
                            } 0%, ${
                              progress >= 50 ? '100%' : '50%'
                            } ${
                              progress >= 50 ? `${100 - (progress - 50)}%` : '50%'
                            }, 50% 50%)`
                          }}
                        ></div>
                        <div className="absolute inset-0 flex items-center justify-center">
                          <span className="text-xs font-medium text-gray-700">{progress}%</span>
                        </div>
                      </div>
                      <p className="text-sm font-medium text-gray-700">{phase.title}</p>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Roadmap Phases */}
            <div className="space-y-6">
              {phases.map((phase, index) => (
                <div key={phase.id} className="relative">
                  {/* Connection Line */}
                  {index < phases.length - 1 && (
                    <div className="absolute left-6 top-20 w-0.5 h-8 bg-gray-300"></div>
                  )}

                  {/* Phase Card */}
                  <div className="bg-white border border-gray-200 rounded-xl overflow-hidden hover:shadow-md transition-shadow">
                    {/* Phase Header */}
                    <div 
                      className="p-6 cursor-pointer"
                      onClick={() => togglePhase(phase.id)}
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex items-center space-x-4">
                          <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center font-bold text-gray-700">
                            {phase.id}
                          </div>
                          <div>
                            <div className="flex items-center space-x-3">
                              <h3 className="text-xl font-bold text-gray-900">{phase.title}</h3>
                              <span className={`px-2 py-1 rounded-full text-xs font-medium border ${getPriorityColor(phase.priority)}`}>
                                {phase.priority}
                              </span>
                            </div>
                            <p className="text-sm text-gray-600 mt-1">{phase.description}</p>
                            <div className="flex items-center space-x-4 mt-2 text-sm text-gray-500">
                              <div className="flex items-center space-x-1">
                                <Target size={14} />
                                <span>{phase.level}</span>
                              </div>
                              <div className="flex items-center space-x-1">
                                <Clock size={14} />
                                <span>{phase.duration}</span>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="flex items-center space-x-3">
                          <div className="text-right">
                            <div className="text-sm text-gray-500">Progress</div>
                            <div className="text-lg font-semibold text-gray-900">{getPhaseProgress(phase)}%</div>
                          </div>
                          <ChevronRight 
                            size={20} 
                            className={`text-gray-400 transition-transform ${
                              expandedPhases.has(phase.id) ? 'rotate-90' : ''
                            }`}
                          />
                        </div>
                      </div>
                    </div>

                    {/* Phase Content */}
                    {expandedPhases.has(phase.id) && (
                      <div className="border-t border-gray-100 bg-gray-50">
                        <div className="p-6">
                          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                            {phase.sections.map((section, sectionIndex) => (
                              <div key={sectionIndex} className="bg-white rounded-lg p-5 border border-gray-200">
                                <h4 className="font-semibold text-gray-900 mb-4 flex items-center">
                                  <div className="w-2 h-2 bg-blue-600 rounded-full mr-3"></div>
                                  {section.title}
                                </h4>
                                <div className="space-y-3">
                                  {section.items.map((item, itemIndex) => {
                                    const itemId = `${phase.id}-${sectionIndex}-${itemIndex}`;
                                    const isCompleted = completedItems.has(itemId);
                                    return (
                                      <div 
                                        key={itemIndex}
                                        className="flex items-start space-x-3 cursor-pointer group"
                                        onClick={() => toggleItem(phase.id, sectionIndex, itemIndex)}
                                      >
                                        <div className="mt-0.5">
                                          {isCompleted ? (
                                            <CheckCircle size={18} className="text-green-600" />
                                          ) : (
                                            <Circle size={18} className="text-gray-400 group-hover:text-gray-600" />
                                          )}
                                        </div>
                                        <span className={`text-sm leading-relaxed transition-colors ${
                                          isCompleted 
                                            ? 'text-gray-500 line-through' 
                                            : 'text-gray-700 group-hover:text-gray-900'
                                        }`}>
                                          {item}
                                        </span>
                                      </div>
                                    );
                                  })}
                                </div>
                              </div>
                            ))}
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>

            {/* Completion Message */}
            <div className="text-center py-12">
              <div className="max-w-md mx-auto">
                <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <Target size={32} className="text-green-600" />
                </div>
                <h2 className="text-2xl font-bold text-gray-900 mb-2">Ready for Your Career</h2>
                <p className="text-gray-600">
                  Complete this roadmap to become a professional product designer with industry-ready skills.
                </p>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'documentation' && (
          <div className="bg-gray-50 rounded-xl p-8 border border-gray-200">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Learning Resources</h2>
            <p className="text-gray-600">Curated resources and documentation for each phase of your learning journey.</p>
          </div>
        )}

        {activeTab === 'projects' && (
          <div className="bg-gray-50 rounded-xl p-8 border border-gray-200">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Portfolio Projects</h2>
            <p className="text-gray-600">Practice projects and case studies to build your portfolio.</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProductDesignerRoadmap;